  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  background-color: var(--back-color);
  color: var(--main-color);
}

.hide-widget {
  visibility: hidden;
}
