.Works__slider-container {
  position: relative;
  z-index: 0;
  width: calc(80% - 1vw);
  margin-left: 1vw;
  height: 75vmin;
  padding: 0 10%;
  border-radius: 5vmax;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  background: var(--second-color);
}
